.VacationCard {
    width: 220px;
    display: inline-flex;
    text-align: center;
    border: solid plum 2px;
    border-radius: 5%;
    font-family: tahoma;
    font-size: small;
    margin: 0.5%;
}

.VacationCard img {
    width: 200px;
    height: 140px;
    margin: 1%;
    align-items: center;
    border-radius: 2%;
    border: 1px solid plum;
    vertical-align: middle;
    justify-content: center;
}

.VacationCard button {
    background-color: transparent;
    border: transparent;
    float: 1px;
}

.CardLogo {
    font-size: medium;
    color: darkcyan;
    font-weight: bold;
}

.follow {
    position: relative;
    padding-left: 60%;
}
